body {
  background-color: #fff;
  margin: 0;
  font-family: Roboto, Arial, sans-serif, Helvetica;
  font-style: normal;
  font-weight: 500;
}

:root {
  --main-color: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  display: flex;
}

.nav__list {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list .nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  text-align: left;
  align-items: center;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active, .nav__link:hover {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.logo-link {
  text-decoration: none;
}

a.logo-link img {
  display: block;
}
